@import "_variables.scss";
@import "_btn-outline.scss";
@import "_social-share.scss";

body {
  font-family: 'tenor Sans',-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";
  font-size: 19px;
  text-transform: none;
  color:#000;
}

.logo {
  max-width:150px;
}

.btn {
  margin-top: 15px;
}
.ui-widget-content {
  background: $dark-yellow;
  width:20px;
}
.slider-vertical {
  .ui-slider-range {
    background: $black;
    width:20px;
  }
}
#handle1, #handle2, #handle3, #handle4, #handle5 {
  width: 3em;
  height: 3em;
  text-align: center;
  line-height: 3em;
  border-radius: 1.5em;
  left:-1em;
  font-weight: 700;
  font-size: 17px;
}

.slider-vertical, .ui-slider-range {
  border-radius: 25px;
  margin: auto;
}

.ui-widget-content {
  .ui-state-active {
    border: 1px solid $gray;
    background: $gray;
  }
}

footer {
  background-color: $dark-yellow;
}

.top-buffer {
  margin-top:20px;
}

#mainNav {
  background-color: $blue;
}

.fine-print {
  font-size: 11px;
}

.focuspoint {
  position: relative; /*Any position but static should work*/
  overflow: hidden;
}
.focuspoint img {
  position: absolute;
  left: 0;
  top: 0;
  margin: 0;
  display: block;
  /* fill and maintain aspect ratio */
  width: auto; height: auto;
  min-width: 100%; min-height: 100%;
  max-height: none; max-width: none;
}


blockquote{
  display:block;
  background: #fff;
  padding: 15px 20px 15px 45px;
  margin: 0 0 20px;
  position: relative;

  /*Font*/
  /*font-family: Georgia, serif;*?
  font-size: 16px;
  line-height: 1.2;
  color: #666;
  text-align: justify;

  /*Borders - (Optional)*/
  border-left: 15px solid $dark-yellow;
  border-right: 2px solid $dark-yellow;

  /*Box Shadow - (Optional)*/
  -moz-box-shadow: 2px 2px 15px #ccc;
  -webkit-box-shadow: 2px 2px 15px #ccc;
  box-shadow: 2px 2px 15px #ccc;
}

blockquote::before{
  content: "\201C"; /*Unicode for Left Double Quote*/

  /*Font*/
  font-family: Georgia, serif;
  font-size: 40px;
  font-weight: bold;
  color: #999;

  /*Positioning*/
  position: absolute;
  left: 10px;
  top:5px;
}

blockquote::after{
  /*Reset to make sure*/
  content: "";
}

blockquote a{
  text-decoration: none;
  background: #eee;
  cursor: pointer;
  padding: 0 3px;
  color: $dark-yellow;
}

blockquote a:hover{
  color: #666;
}

blockquote em{
  font-style: italic;
}

.image-cropper {
  width: 100px;
  height: 100px;
  position: relative;
  overflow: hidden;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  border-radius: 50%;

  img {
    display: inline;
    margin: 0 auto;
    height: 100%;
    width: auto;
  }
}

.left {
  float:left;
}

.vertical-center {
  vertical-align: middle;
}

.blog-section {
  margin: 30px 0;

}


#blog {

  h1 {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    font-size: 55px;
    font-family: "Archivo Black";
    color: $blue;
  }

  h2 {
    font-size: 45px;
    font-family: "Archivo Black";
    color: $blue;
  }

  h3 {
    font-size: 35px;
    font-family: "Archivo Black";
    color: $dark-yellow;
  }

  .center {
    text-align: center;
    margin: auto;
  }
}

.brand-meter {
  max-width: 100px;

  img {
    max-width: 100%;
    max-height: 40px;
    margin: 10px auto;
  }
}

.form-control {
  &.sign-up {
    height: 70px;
    margin-top: 15px;
    width: 300px;
  }
}

.navbar-brand {
  height: auto;
}

.navbar-custom {
  .nav {
    li {
      a {
        font-family: "Tenor Sans";
        letter-spacing: -.5px;
        padding-top: 23px;
        font-size: 15px;
      }
    }
  }
}

.form-control {
  font-family: "Tenor Sans";
  letter-spacing: -.5px;
}

.brand-title {
  font-family: "Tenor Sans";
  color: #121212;
  letter-spacing: -.5px;
}

.brand-description {
  letter-spacing: -.5px;
}

.science-sub {
  letter-spacing: -.5px;
}

.cta {
  background-color: $blue;
  padding: 20px;
  color: white;
}

.author {
  height: 50px;
  padding-top: 15px;
}

.cta-image {
  img {
    max-width: 100%;
  }
}

.cta-section {
  button {
    max-width:100%;
    white-space: normal;
  }
}

.blog-row {
  margin-left: 0;
  margin-right: 0;
}

img {
  max-width: 100%;
}

.paddingtop20 {
  padding-top: 20px;
}

.intro-text {
  input {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 425px) {
  #blog {

    h1 {
      text-align: center;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
      font-size: 35px;
      font-family: "Archivo Black";
      color: $blue;
    }

    h2 {
      font-size: 25px;
      font-family: "Archivo Black";
      color: $blue;
    }

    h3 {
      font-size: 20px;
      font-family: "Archivo Black";
      color: $dark-yellow;
    }

    .center {
      text-align: center;
      margin: auto;
    }
  }
}
